<template>
  <div class="item">
    <div class="block" @click="isEdit = !isEdit">
      <img
        v-if="item.type === 'Image'"
        :src="text"
        :alt="item.type"
      >

      <h1 v-if="item.type === 'Headline'">
        {{text}}
      </h1>

      <p v-if="item.type === 'Paragraph'">
        {{text}}
      </p>

      <button v-if="item.type === 'Button'">
        {{text}}
      </button>
    </div>

    <div class="edit-panel" v-if="isEdit">
      <input v-model="text" type="text">
      <button v-on:click="deleteItem(item.id)">delete</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Block',
  props: ['item'],
    components: {
  },
  data: () => {
    return {
      text: "https://ru.vuejs.org/images/components.png",
      isEdit: false,
    }
  },
  mounted() {
  },
  methods: {
    deleteItem(id) {
      this.$emit('deleteItem', id);
    }
  }
}
</script>

<style>
  img { 
    width: 400px;
  }
</style>
